body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
  background-color: #fff;
  height: 100vh;
}

.App-header {
  background-color: #fff;
  padding: 20px;
  border-bottom: 1px solid #e0e0e0;
}

.App-body {
  padding: 20px;
}

.App-footer {
  padding: 10px;
  border-top: 1px solid #e0e0e0;
  background-color: #fff;
  position: absolute;
  width: 100%;
  bottom: 0;
}

.App-header img {
  height: 40px;
  margin-right: 20px;
  vertical-align: middle;
}

.App-header h1 {
  display: inline-block;
  vertical-align: middle;
  color: #e1354b;
  font-size: 2rem;
}
